export function getSongVKey(songMid) {
    console.log(songMid)
    const data={
        "comm":{
            "g_tk":5381,
            "uin":0,
            "format":"json",
            "inCharset":"utf-8",
            "outCharset":"utf-8",
            "notice":0,
            "platform":"h5",
            "needNewCode":1
        },
        "url_mid":{
            "module":"vkey.GetVkeyServer",
            "method":"CgiGetVkey",
            "param":{
                "guid":"2101435675",
                "songmid":[songMid],
                "songtype":[0],
                "uin":"0",
                "loginflag":0,
                "platform":"23"
            }
        }
    };
    var timestamp = Date.parse(new Date());
    return POST(`https://u.y.qq.com/cgi-bin/musicu.fcg?_=${timestamp}`,JSON.stringify(data))
}


