export function POST(url,data) {

    var settings = {
        "async": true,
        "crossDomain": true,
        "url": url,
        "method": "POST",
        "data":data,
    }
   return new Promise((resolve, reject)=>{

           $.ajax(settings).done((res)=>{
               resolve(res)
           })
   }
   )
}

