import React from 'react'
import Swiper from 'swiper'
import "swiper/dist/css/swiper.css"
import {getCarousel,getNewAlbum} from "../../api/recommend"
import {CODE_SUCCESS} from "../../api/config"
import * as AlbumModel from '../../model/album'
import Scroll from '../../common/scroll/Scroll'
import Loading from '../../common/Loading/Loading'
import Lazyload,{forceCheck} from 'react-lazyload'
import {Route} from 'react-router-dom'
import Album from '../../containers/AlbumContainers'
import './recommend.styl'



class Recommend extends React.Component {

    constructor(props){
        super(props);
        console.log(props);
        this.state = {
            loading:true,
            sliderList:[],
            newAlbums:[],
            refreshScroll:false
        };
    }

    componentDidMount(){


        getNewAlbum().then((res)=>{
           if (res){
               console.log(res);
               if (res.code === CODE_SUCCESS){
                   // 
                   let albumList = res.albumlib.data.list;
                   albumList.sort((a,b)=>{
                       return new Date(b.public_time).getTime() > new Date(a.public_time).getTime();
                   });
                   this.setState({
                       loading:false,
                       newAlbums:albumList
                   },()=>{
                       this.setState({refreshScroll:true});
                   });
               }
           }

        });

        getCarousel().then((res)=>{

            if (res){
                console.log(res);
                if (res.code === CODE_SUCCESS){
                    this.setState({
                        sliderList:res.data.slider
                    },()=>{
                        if (!this.sliderSwiper){
                            // 
                            this.sliderSwiper = new Swiper(".swiper-container",{
                                pagination:'.swiper-pagination',
                                paginationClickable: true,
                                speed: 500,
                                loop: true,
                                observer:true,
                                observeParents:true,
                                autoplayDisableOnInteraction : false,
                                autoplay:3000
                            })

                        }

                    });
                }

            }

        });
    }

    /* */
    toLink(linkUrl){
        return () => {
            window.location.href = linkUrl
        }
    }
    /**/
    toAlbumDetail(url){

        return ()=>{
            this.props.history.push({
                pathname:url
            })
        }

    }

    render(){

       let {match} = this.props;
        // Item
       let albums = this.state.newAlbums.map(item=>{
          // 
          let album =  AlbumModel.createAlbumByList(item);

          return (
            <div className="album-wrapper" key={album.mId} onClick={this.toAlbumDetail(`${match.url+'/'+ album.id}`)}>
               <div className="left">
                   <Lazyload>
                   <img src={album.img} width="100%" height="100%" alt={album.name} />
                   </Lazyload>
               </div>
                <div className="right">
                    <div className="album-name">
                        {album.name}
                    </div>
                    <div className="singer-name">
                        {album.singer}
                    </div>
                    <div className="public-time">
                        {album.publicTime}
                    </div>
                </div>
            </div>
          );

       });



        return(
          <div className="music-recommend">
              <Scroll refresh={this.state.refreshScroll} onScroll={(scroll)=>{
               //lazyload scroll  better-scrollcssTransform; 
                  forceCheck();
              }}>
              <div>
              <div className="swiper-container">
                  <div className="swiper-wrapper">
                      {
                          this.state.sliderList.map(slider=>{
                              return(
                                  <div className="swiper-slide" key={slider.id}>
                                      <a className="slider-nav" onClick={this.toLink(slider.linkUrl)}>
                                          <img src={slider.picUrl} width="100%" height="100%" alt=""/>
                                      </a>
                                  </div>
                              );
                          })
                      }
                  </div>
                  <div className="swiper-pagination"></div>
              </div>
              <div className="album-container">
                  <h1 className="title"></h1>
                  <div className="album-list">
                      {albums}
                  </div>
              </div>
              </div>
              </Scroll>
              <Loading  show={this.state.loading} title="..."/>
              <Route path={`${match.url+'/:id'}`} component={Album}/>
          </div>

        );
    }

}


export default  Recommend


