import React from 'react'
import ReactDOM from 'react-dom'
import './album.styl'
import Header from '../../common/header/Header'
import Scroll from '../../common/scroll/Scroll'
import Loading from '../../common/Loading/Loading'
import  {getAlbumInfo} from "../../api/recommend"
import {CODE_SUCCESS, URL} from "../../api/config"
import  * as AlbumModel from '../../model/album'
import  * as SongModel from  '../../model/song'
import {getSongVKey} from "../../api/song"
import {CSSTransition} from 'react-transition-group'






class Album extends React.Component {


    constructor(props){
        super(props)
        this.state = {
            show:false,
            loading:true,
            album:{},
            songs:[],
            refreshScroll:false,
            bgMountHeight:0
        }
    }

    componentDidMount(){

        this.setState({show:true});

        let albumBgDom = ReactDOM.findDOMNode(this.refs.albumBg);
        let albumContainerDom = ReactDOM.findDOMNode(this.refs.albumContainer);
        albumContainerDom.style.top = albumBgDom.offsetHeight + "px";

       /*  */
        getAlbumInfo(this.props.match.params.id).then((res)=>{
          if (res){
              console.log(res);
              if (res.code === CODE_SUCCESS){
                  let album =AlbumModel.creatAlbumByDetail(res.data);
                  album.desc = res.data.desc;

                  let songList = res.data.list;
                  let songs =[];
                  songList.forEach(item=>{
                      let song = SongModel.createSong(item);
                      this.getSongUrl(song,item.songmid);
                      songs.push(song);
                  });

                  this.setState({
                      loading:false,
                      album:album,
                      songs:songs,
                      refreshScroll:true,
                      bgMountHeight: ReactDOM.findDOMNode(this.refs.albumBg).offsetHeight
                  });
              }
          }

        });
    }


    /*  */
    getSongUrl(song,mId) {
        console.log(song.mId)
      getSongVKey(song.mId).then((res)=>{
            res=JSON.parse(res)
          if (res){
              if (res.code === CODE_SUCCESS){
                  let item = res.url_mid.data.midurlinfo[0];
                  song.url = `http://dl.stream.qqmusic.qq.com/${item.purl}`
                  console.log(song.url)
              }
          }
      })

    }

    // 
    selectSong = (song)=> {
        return (e)=>{
          this.props.setSongs([song]);
          this.props.changeCurrentSong(song);
        };
    }


    // 
    scrolling = ({y})=>{
        let albumBgDOM = ReactDOM.findDOMNode(this.refs.albumBg);
        if (y>0){
            //
            let transform = `scale(${1+y*0.005},${1+y*0.005})`;
            albumBgDOM.style.transform = transform;
            albumBgDOM.style.zIndex = '0';
            albumBgDOM.style.paddingTop = '70%'

        }else {
            // 
            albumBgDOM.style.transform = 'scale(1,1)';
             if (Math.abs(y)+55 > this.state.bgMountHeight){
                 // Header
                 // z-index  
                 albumBgDOM.style.zIndex = '1';
                 albumBgDOM.style.paddingTop = '55px';
             }else {
                   albumBgDOM.style.paddingTop = '70%';
                   albumBgDOM.style.zIndex = '0';
             }
        }

    }


    playAll =()=>{
        if (this.state.songs.length){
            // 
            this.props.setSongs(this.state.songs);
            this.props.changeCurrentSong(this.state.songs[0]);
            // show
            this.props.showMusicPlayer(true);
        }
    }

    render(){

        let album = this.state.album;
        let songs = this.state.songs.map((song)=>{
            return(
              <div className="song" key={song.id} onClick={this.selectSong(song)}>
                  <div className="song-name">{song.name}</div>
                  <div className="song-singer">{song.singer}</div>
              </div>
            );
        });

        return (
            <CSSTransition classNames="translate" in={this.state.show} timeout={300} >
          <div className="music-album">
           <Header title={album.name} ref="Header"/>
           <div style={{position:"relative"}}>
               <div ref="albumBg" className="album-img" style={{backgroundImage:`url(${album.img})`}}>
                   <div className="filter"></div>
               </div>
               <div className="play-wrapper" ref="playButton">
               <div className="play-button" onClick={this.playAll}>
                   <i className="icon-play"></i>
                   <span></span>
               </div>
               </div>
           </div>
              <div ref="albumContainer" className="albuminfo-container">
                  <div className="album-scroll">
                      <Scroll refresh={this.state.refreshScroll} onScroll={this.scrolling}>
                          <div className="albuminfo-wrapper">
                              <div className="song-count"> {songs.length}</div>
                              <div className="song-list">
                                  {songs}
                              </div>
                              <div className="album-info">
                                  <h1 className="album-title"></h1>
                                  <div className="album-desc">
                                      {album.desc}
                                  </div>
                              </div>
                          </div>
                      </Scroll>
                  </div>
                  <Loading title="..." show={this.state.loading}/>
              </div>
          </div>
            </CSSTransition>
        );
    }
}

export default  Album


