import React from 'react'
import Player from '../../containers/PlayContainer'





class MusicPlayer extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            show:false,
            currentIndex:0
        }
    }

    changeCurrentIndex =(index)=>{
        this.setState({
            currentIndex:index
        })
    };

    showList=(status)=>{
        this.setState({
            show:status
        })
    };




    render(){

        return(
            <div>
                <Player currentIndex={this.state.currentIndex}
                        changeCurrentIndex={this.changeCurrentIndex}
                        />
            </div>
        )
    }
}


export default MusicPlayer;


