import React from 'react'
import ReactDOM from 'react-dom'
import {Song} from "../../model/song"
import Progress from './Progress'
import {CSSTransition} from 'react-transition-group'

import './player.styl'

class Player extends React.Component {

    constructor(props){
        super(props)

        //initial
        let song = new Song(
            201773975,
            "000OFXjz0Nljbh",
            "",
            "https://y.gtimg.cn/music/photo_new/T002R300x300M000002QIEJo1LUz7p.jpg?max_age=2592000",
            244,
            "http://dl.stream.qqmusic.qq.com/C400000OFXjz0Nljbh.m4a?vkey=33E6799DB60508E9561F3DDAA6ED69488988B51DE791617A2E34B02A3A48E9CCBD43595DC00D796EB6768D26134AAB1C3AD192C7044DE1A1&guid=3655047200&fromtag=66",
            "/"
        );
        this.currentSong = song
        this.currentIndex = 0;
        this.playModels = ['list','single','shuffle'];
        this.state = {
           currentTime:0,
           currentPlayModel:0,
           playStatus:false,
           playProgress:0
        };
    }

    componentDidMount(){
        this.playBgDOM = ReactDOM.findDOMNode(this.refs.playBg);
        this.singerImgDOM = ReactDOM.findDOMNode(this.refs.singerImg);
        this.audioDOM = ReactDOM.findDOMNode(this.refs.audio);
        this.playWrapperDOM = ReactDOM.findDOMNode(this.refs.playerWrapper);


        this.audioDOM.addEventListener('canplay',()=>{
            //  
            this.audioDOM.play();
            this.startRotateImage();

            this.setState({
                playStatus:true
            });

        },false);

        this.audioDOM.addEventListener('timeupdate',()=>{
            if (this.state.playStatus) {
                this.setState({
                    playProgress:this.audioDOM.currentTime / this.audioDOM.duration,
                    currentTime:this.audioDOM.currentTime
                })
            }

        },false);

        this.audioDOM.addEventListener('ended',()=>{

            if (this.props.playSongs.length > 1){
                let currentIndex = this.currentIndex;
                if (this.state.currentPlayModel === 0) {
                    // list
                    if(currentIndex === this.props.playSongs.length){
                        currentIndex = 0;
                    } else {
                        currentIndex += 1;
                    }
                }else if (this.state.currentPlayModel === 1){
                    // single 
                    this.audioDOM.play();
                    return;
                }else {
                    // 
                    currentIndex =  parseInt(Math.random() * this.props.playSongs.length,10);
                }
                this.props.changeCurrentSong(this.props.playSongs[currentIndex]);
                this.props.changeCurrentIndex(currentIndex);

            }else {
                if (this.state.currentPlayModel === 1){
                    this.audioDOM.play();
                }else {
                    this.audioDOM.pause();
                    this.stopRotateImage();
                    this.setState({
                        playProgress:0,
                        currentTime:0,
                        playStatus:false
                    })
                }


            }


        },false);


        this.audioDOM.addEventListener('error',()=>{
            alert('');
        });
    }

    /* / */
    playOrPause= ()=> {
        if (this.audioDOM.paused) {
            this.audioDOM.play();
            // 
            this.startRotateImage();

            this.setState({
                playStatus:true
            });
        }else{
            this.audioDOM.pause();
            // 
            this.stopRotateImage();
            this.setState({
                playStatus:false
            });
        }
   }

   /**/
    previous=()=>{
        console.log(this.props.playSongs)
       if (this.props.playSongs.length > 0 && this.props.playSongs.length !== 1){
          let currentIndex = this.currentIndex;
           if (this.state.currentPlayModel === 0){
               //list 
               if (currentIndex === 0) {
                   currentIndex = this.props.playSongs.length -1;
               }else {
                   currentIndex -= 1;
               }
           }else if (this.state.currentPlayModel === 1){
               // single
               currentIndex = this.currentIndex;
           }else{
               // 
               let index = parseInt(Math.random() * this.props.length,10);
               currentIndex = index;
           }

           //
           this.props.changeCurrentSong(this.props.playSongs[currentIndex]);
           this.props.changeCurrentIndex(currentIndex);


       }

   }

   /**/
  next=()=>{
      if (this.props.playSongs.length > 0 && this.props.playSongs.length !== 1){
          let currentIndex = this.currentIndex;
             if (this.state.currentPlayModel === 0){
                 //list
                 if (currentIndex === this.props.playSongs.length -1){
                     currentIndex = 0;
                 }else {
                     currentIndex += 1;
                 }
             }else if (this.state.currentPlayModel === 1){
                 // single
                 currentIndex = this.currentIndex;
             }else {
                 //
                 let index = parseInt(Math.random() * this.props.length,10);
                 currentIndex = index;
             }
             // 
          this.props.changeCurrentSong(this.props.playSongs[currentIndex]);
          this.props.changeCurrentIndex(currentIndex);

      }

  }

  // 
    startRotateImage=()=>{
      if (this.singerImgDOM.className.indexOf('rotate') === -1){
        this.singerImgDOM.classList.add('rotate');
      }else {
          this.singerImgDOM.style.webkitAnimationPlayState = 'running';
          this.singerImgDOM.style.animationPlayState = 'running';
      }
    }

    // 
    stopRotateImage=()=>{
        this.singerImgDOM.style.webkitAnimationPlayState = 'paused';
        this.singerImgDOM.style.animationPlayState = 'paused';
    }



    //
    changePlayMode=()=>{
        if (this.state.currentPlayModel === this.playModels.length -1) {
            this.setState({currentPlayModel:0});
        }else {
            this.setState({currentPlayModel:this.state.currentPlayModel+1});
        }
    }

    // 
    showPlayer(){
        this.props.showMusicPlayer(true);
    }

    //
    hidePlayer =()=>{
        this.props.showMusicPlayer(false);
    }



    render(){
        //this.currentSong = this.props.currentSong
        this.currentIndex = this.props.currentIndex;
        console.log(this.currentSong)
        // redux
        if (this.props.currentSong && this.props.currentSong.url){
            // 
            if(this.currentSong.id !== this.props.currentSong.id) {
                this.currentSong = this.props.currentSong;
                this.audioDOM.src = this.currentSong.url;
                console.log('bofang')
                // iOS 
                this.audioDOM.load();
            }
        }


        let song = this.currentSong;
        let playBg = song.img?song.img:require('../../assets/imgs/play_bg.jpg');
        let playButton = this.state.playStatus === true? "icon-pause":"icon-play";
        song.playStatus = this.state.playStatus;

        return (
          <div className="player-container">
              <CSSTransition in={this.props.showStatus} timeout={300} classNames="player-rotate" onEnter={()=>{
                  this.playWrapperDOM.style.display = 'block';
              }} onExited={()=>{
                  console.log('wwww');
                  this.playWrapperDOM.style.display = 'none';
              }}>
              <div className="player-wrapper" ref='playerWrapper'>
                  <div className="player-up">
                  <div className="header">
                      <span className="header-back" onClick={this.hidePlayer}>
                          <i className="icon-back"></i>
                      </span>
                      <div className="header-title">
                          {song.name}
                      </div>
                  </div>
                      <div className="player-middle">
                          <div className="singer-name">{song.singer}</div>
                          <div className="singer-img rotate" ref="singerImg">
                              <img src={playBg} alt={song.name} onLoad={(e)=>{

                                  /**/
                                  this.playBgDOM.style.backgroundImage = `url(${playBg})`

                              }}/>
                          </div>
                      </div>

                  <div className="player">
                      <div className="player-bottom-top">
                      <span className="current-time">{getTime(this.state.currentTime)}</span>
                      <div className="play-progress">
                          <Progress progress={this.state.playProgress} disableButton={false} disableDrag={false} onDragStart={(e)=>{

                          }} ondrag={(e)=>{}} ondragend={(e)=>{}}/>
                      </div>
                      <span className="total-time">{getTime(song.duration)}</span>
                      </div>
                      <div className="player-bottom-bottom">
                          <div className="play-mode" onClick={this.changePlayMode}>
                              <i className={'icon-'+this.playModels[this.state.currentPlayModel]+'-play'}></i>
                          </div>
                          <div className="previous" onClick={this.previous}>
                              <i className='icon-previous'></i>
                          </div>
                          <div className="player-button" onClick={this.playOrPause}>
                              <i className={playButton}></i>
                          </div>
                          <div className="next" onClick={this.next}>
                              <i className='icon-next'></i>
                          </div>
                          <div className="play-list-button">
                              <i className='icon-play-list'></i>
                          </div>
                      </div>
                      <audio ref="audio"></audio>
                  </div>
                  </div>
                  <div className="player-background" ref="playBg"></div>
              </div>
              </CSSTransition>
          </div>
        );

    }

}

function getTime(seconds) {
    seconds = Math.floor(seconds);
    let minute = Math.floor(seconds / 60);
    let second = seconds - minute * 60;

    return minute+":"+formatSecond(second);
}

function formatSecond(second) {
    let timeStr = "00";
    if (second < 10){
        timeStr = '0'+second;
    }else {
        timeStr=second;
    }
    return timeStr;
}

export default Player


