import React from 'react'
import PropTypes from 'prop-types'
import ReactDOM from 'react-dom'



import './progress.styl'

class Progress extends React.Component {


    componentDidUpdate(){

        if (!this.progressBarWidth) this.progressBarWidth = ReactDOM.findDOMNode(this.refs.progressBar).offsetWidth;
    }

  componentDidMount(){
  let progressBarDOM = ReactDOM.findDOMNode(this.refs.progressBar);
  let progressDOM = ReactDOM.findDOMNode(this.refs.progress);
  let progressBtnDOM = ReactDOM.findDOMNode(this.refs.progressBtn);
  this.progressBarWidth = progressBarDOM.offsetWidth;


  let {disableButton,disableDrag,onDragStart,onDrag,onDragEnd} = this.props;

  if (disableButton !== true && disableDrag !== true){

      //
      let downX = 0;
      let btnLeft = 0;

      progressBtnDOM.addEventListener('touchstart',(e)=>{
              let touch = e.touches[0];
              downX = touch.clientX;
              btnLeft = parseInt(touch.target.style.left);
              if (onDragStart){
                  onDragStart();
              }

      });


      progressBtnDOM.addEventListener('touchmove',(e)=>{
          // 
          e.preventDefault();

          let touch = e.touches[0];
          let diffX = touch.clientX - downX;

          let buttonLeft = btnLeft + diffX;
          if (buttonLeft > progressBarDOM.offsetWidth) buttonLeft = progressBarDOM.offsetWidth;
          if (buttonLeft < 0) buttonLeft = 0;

          e.target.style.left = buttonLeft;
          progressDOM.style.width = buttonLeft;
          if (onDrag) onDrag(buttonLeft / this.progressBarWidth);


      });


      progressBtnDOM.addEventListener('touchend',(e)=>{


          if (onDragEnd) onDragEnd();


      });


    }

  }

    render(){
      let {progress,disableButton} = this.props;
      let progressBtnOffsetLeft = 0;
      if (this.progressBarWidth){
          progressBtnOffsetLeft = progress * this.progressBarWidth;
      }
        return(
          <div className="progress-bar" ref="progressBar">
              <div className="progress-load"></div>
              <div className="progress" ref="progress" style={{width:`${progress*100}%`}}></div>
              {
                  disableButton === true?'':<div className="progress-button" ref="progressBtn" style={{left:progressBtnOffsetLeft}}></div>
              }
          </div>
        );
    }

}

Progress.defaultProps = {
    progress:0,
    disableButton:true,
    disableDrag:true,
    onDragStart:null,
    onDrag:null,
    onDragEnd:null

};


Progress.propTypes = {

    progress:PropTypes.number,
    disableButton:PropTypes.bool,
    disableDrag:PropTypes.bool,
    onDragStart:PropTypes.func,
    onDrag:PropTypes.func,
    onDragEnd:PropTypes.func

};



export default Progress






