import {combineReducers} from 'redux'
import * as ActionTypes from './actionTypes'

/* state  */


const initialState = {
    showState:false,
    song:{},
    songs:[]
}

// /
function showStatus(showStatus = initialState.showState,action) {
    switch (action.type){
        case ActionTypes.SHOW_PLAYER:
            console.log(action)
            return action.showStatus;
        default:
            return showStatus;
    }
}
/* Change song */
function song(song = initialState.song,action) {
    switch (action.type){
        case ActionTypes.CHANGE_SONG:
            return action.song;
        default:
            return song;
    }
}
// 
function songs(songs = initialState.songs,action) {
    switch(action.type){
        case ActionTypes.SET_SONGS:
            return action.songs;
        case  ActionTypes.REMOVE_SONG_FROM_LIST:
            return songs.filter(song=>song.id !== action.id);
        default:
            return songs;
    }
}
// Reducer
const reducer = combineReducers({
    showStatus,
    song,
    songs
});


export default reducer


